
body {
  margin: 0;
  padding: 0;
}
#app {
  font-family: 'Roboto', Helvetica, Arial, sans-serif;
  font-smoothing: antialiased;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
#app .main,
#app .sideNav {
  min-height: 100vh;
  width: 100%;
}
#app .sideNav {
  width: 240px;
  background-color: #263238;
}
#app .sideNav h2.logo {
  padding: 50px 0;
  text-align: center;
  color: #fff;
}
#app .main {
  background-color: #fffde7;
  padding: 0 5rem;
}


