
.FeedList__Item {
  display: block;
  position: relative;
  background-color: #455a64;
  margin: 2px 0;
  padding: 0.8rem 0.3rem;
  color: #fff;
  transition: 150ms background-color ease-in-out;
}
.FeedList__Item span {
  margin-left: 1rem;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  width: 120px;
  display: inline-block;
  font-weight: bold;
  font-size: 0.9rem;
}
.FeedList__Item a.deleteItem {
  text-align: center;
  position: absolute;
  right: 0;
  width: 3rem;
}
.FeedList__Item:hover,
.FeedList__Item--current {
  background-color: transparent;
  color: #9e9e9e;
}
.FeedList__Item:hover::before,
.FeedList__Item--current::before,
.FeedList__Item:hover::after,
.FeedList__Item--current::after {
  content: '';
  display: block;
  position: absolute;
  top: 0;
}
.FeedList__Item:hover::before,
.FeedList__Item--current::before {
  width: 2px;
  background-color: #ff3d00;
  height: 100%;
  left: 0;
  transition: 150ms width ease-in;
}
.FeedList__Item--current {
  color: #fff;
}
.FeedList__Item--current::before {
  width: 4px;
}
.FeedList__Item--current::after {
  margin: auto;
  right: 0;
  top: 0;
  bottom: 0;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 8px 8px 8px 0;
  border-color: transparent #fffde7 transparent transparent;
  line-height: 0px;
}


