
.FeedPane__loader {
  height: calc(75vh - 145px);
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.FeedPane__list {
  display: block;
  margin: 2px 0;
  padding: 3px 7px;
/*background-color*/
}
.FeedPane__list h1 {
  margin: 0px 0 9px;
  font-size: 3rem;
  font-weight: 100;
}
.FeedPane__list .articles {
  height: calc(100vh - 290px);
  overflow: auto;
}
.FeedPane__list .article {
  margin: 3px 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  background-color: #fff;
}
.FeedPane__list .article:nth-child(odd) {
  background-color: #ebebeb;
}
.FeedPane__list .article img {
  height: 120px;
  width: 200px;
  padding: 5px 3px;
  -o-object-fit: cover;
     object-fit: cover;
}
.FeedPane__list .article a {
  color: #0288d1;
  text-decoration: none;
}
.FeedPane__list .article__details {
  margin-left: 25px;
  padding: 10px 0;
}
.FeedPane__list .article__details> a {
  font-size: 1.3rem;
}
.FeedPane__list .article__details p {
  margin: 0;
  padding: 0.8rem 0 0;
  font-size: 0.9rem;
  color: #6b6b6b;
}
.FeedPane__list .article__details p>p {
  margin: 0;
  padding: 0;
}
.FeedPane__list .article__details p>a img {
  margin: 0 10px;
}


